/* Features Block */

.feature {
    gap: 4rem 2rem;
    margin: 5rem 0;
}

.feature:first-of-type {
    margin-top: 5rem;
}

.feature__heading {
    margin: 1rem 0;
}

.feature__image {
    width: 100%;
}


 /* .block-feature__image img {
     width: 100%;
     padding: 4rem;
}

.block-feature__content {
    padding: 4rem;
    align-self: center;
} */

@media screen and (min-width: 768px) {
    .feature:nth-of-type(even) .feature__content {
        order: 2;
    }
}

/* Showcase Block*/

.block-showcase__image > img {
    width: 100%;
}

@media screen and (min-width: 768px) {
    .block-showcase .grid {
        grid-template-columns: 50% 50%;
    }
    
    .block-showcase__image {
        justify-self: end;
    }

    .block-showcase__image > img {
        width: auto;
        max-width: 700px;
    }
}

.showcase .media__body {
    color: rgb(149, 145, 145);
}

/* Footer --commented out stuff was my attempt*/

.footer {
    background: #232323;
}

.footer a {
    color: #777;
    transition: color .3s;
}

.footer a:hover {
    color: #fff;
}

.footer__section {
    padding: 2rem;
    border-bottom: 1px solid #393939;
}

.footer .list {
    margin: 0;
}

.footer__heading {
    text-transform: uppercase;
    font-weight: 600;
}

.footer__brand {
    margin-top: 5rem;
    text-align: center;
}

.footer__brand img {
    width: 100%;
    max-width: 230px;
}

.footer__copyright {
    font-size: 2.1rem;
    color: #fff;
    opacity: .3;
}

@media screen and (min-width: 768px) {
    .footer__sections {
        grid-template-columns: repeat(auto-fit, minmax(10rem, 1fr));
    }

    .footer .collapsible__icon {
        display: none;
    }

    .footer .collapsible__content {
        opacity: 1;
        max-height: 100%;
    }

    .footer .list {
        font-size: 1.5rem;
    }
    
    .footer__brand {
        order: -1;
        margin-top: 1rem;
    }

    .footer__copyright {
        font-size: 1.5rem;
    }

    .footer__section {
        border: 0;
    }

    .footer__heading {
        font-size: 1.6rem;
    }

}

